.mainBasketItem{
    width: 100%;
    /* max-height: 50px; */
    /* background-color: #fff; */
    border: solid 1px red;
    /* border-radius: 5px; */
    margin-bottom: 5px;
    text-transform: capitalize;
    background-color: #161616;
    box-shadow: 0px 0px 50px 1px rgba(0,0,0,1);

}

.mainBasketBoxItem{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
}

.mainBasketBoxTitle{
    margin:  2px 6px;
    margin-bottom: 25px;
}

.mainBasketBoxItemImage{
    position: relative;
    min-height: 100%;
    width: 100px;
}

.mainBasketBoxItemImage img{
    /* border-radius: 5px 0 0 5px; */
    border-right: 1px solid red;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.mainBasketBoxTitle span{
    font-size: 15px;
}

.mainBasketBoxDelete{
    position: absolute;
    bottom: -1px;
    right: -1px;
    height: 20px;
    display: flex;
}

.mainBasketBoxDelete button{
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid red;
    background-color: #161616;
    color: #fff;
    height: 20px;
    padding: 0 10px 0 10px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 5px 0 0 0;

}

.mainBasketBoxDelete button:hover{
    background-color: #121212;
}

.mainBasketBoxDelete span{
    margin-right: 10px;
}
.mainBasket{
    scale: 0;
    position: absolute;
    width: 600px;
    min-height: 100px;
    max-height: 600px;
    right: -40px;
    top: 60px;
    background-color: #161616;
    border-radius: 10px;
    border: solid red 3px;
    box-shadow: 0px 0px 500px 900px rgba(0, 0, 0, 0.781);
    transition: 0.2s;
    overflow-y: auto;
    overflow-x: hidden;
}

.mainBasketBox{
    width: 100%;
    height: 100%;
    padding: 10px;
}

.mainBasketCloseBox{
    position: absolute;
    right: 36px;
    top: -63px;
    width: 45px;
    height: 40px;
    transition: 0.2s;
}

.mainBasketTotal{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    margin-bottom: 10px;
    font-weight: 600;
}

.mainBasketTotal button{
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid red;
    background-color: #161616;
    color: #fff;
    padding: 0 10px 0 10px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 5px;
    transition: .2s;
}

.mainBasketTotal button:hover{
    border-color: green;
    scale: 1.1;
}

.mainNavbar{
    position: absolute;
    width: 100vw;
    height: 70px;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 70px;
    color: #fff;
    text-transform: uppercase;
}

.mainNavbarBasket{
    position: absolute;
    width: 45px;
    height: 45px;
    right: 110px;
    transition: 1s;
}

.mainNavbarBasket img{
    cursor: pointer;
    transition: .2s;
}

.mainNavbarBasket img:hover{
    scale: 1.2;
}


.mainNavbarBasketCountBox{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40%;
    height: 40%;
    position: absolute;
    bottom: -10px;
    right: -10px;
    color: red;
    transition: .2s;
}

.mainNavbarList{
    display: flex;
    list-style: none;
    transition: 1s;
}
.mainNavbarList li{
    margin-right: 70px;
    font-weight: 500;
    cursor: pointer;
    transition: .2s;
    position: relative;
}
.mainNavbarList li:hover{
    color: #fff;
    scale: 1.1;
}
.mainNavbarList li::before{
    content: "";
    width: 0;
    height: 2px;
    position: absolute;
    background-color: #fff;
    z-index: -1;
    bottom: 0;
    transition: .2s;

}
.mainNavbarList li:hover::before{
    width: 100%;
}

.mainNavbarH1{
    text-align: center;
    width: 200px;
    height: 50px;
    position: relative;
    cursor: pointer;
}

.mainNavbarH1other{
    position: absolute;
    width: 0px;    
    transition: 1s;
    bottom: 0;
}

.mainNavbarH1:hover .mainNavbarH1other{
    background-color: red;
    width: 100%;
    height: 3px;
}

.mainNavbarH1:not(:hover) .mainNavbarH1other{
    background-color: red;
    width: 0;
    height: 3px;
}

