.menuItemMain{
    position: relative;
    width: 100%;
    /* min-height: 150px; */
    border: solid 3px red;
    background-color: #161616;
    border-radius: 10px;
    transition: .2s;
    margin-bottom: 10px;
    cursor: pointer;
}

#hover.menuItemMain:hover{
    scale: 1.01;
}

.menuItemContents{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
}

.menuItemImage{
    min-height: 100%;
    width: 250px;
    border-right: 3px solid red;
    transition: .2s;
}

.menuItemImage img{
    border-radius: 6px 0 0 6px;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.menuItemDescContents{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* text-transform: uppercase; */
    color: #fff;
    margin: 10px;
}

.menuItemDescContents span{
    white-space: pre-wrap;
    font-weight: 600;
}

.menuItemPrice{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: -2px;
    right: -2px;
    color: red;
    /* width: 70px; */
    padding: 10px;
    height: 30px;
    font-size: 20px;
    border: solid red 3px;
    border-radius: 10px 0;
    font-weight: 100;
    background-color: #161616;
}

.menuItemBtns{
    position: absolute;
